import { Component, Input, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { AccommodationReviews } from '@schemas/modules/accommodations/accommodation-reviews.model';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-accommodation-nearby-places',
    templateUrl: './accommodation-nearby-places.component.html',
    styleUrls: ['./accommodation-nearby-places.component.scss'],
})
export class AccommodationNearbyPlacesComponent implements AfterViewInit {
    @ViewChild('pointOfInterest', { read: ElementRef, static: false }) pointOfInterest!: ElementRef;
    @ViewChild('placeToEat', { read: ElementRef, static: false }) placeToEat!: ElementRef;
    @Input() accommodationReviews!: AccommodationReviews;

    public showAll = true;
    public totalLines = 0;

    constructor(public platformService: PlatformService) {}

    ngAfterViewInit(): void {
        const lines = (el: HTMLDivElement): number => {
            if (!el) {
                return 0;
            }

            const lineHeight = 24;
            return el.offsetHeight / lineHeight;
        };

        this.totalLines = Math.max(lines(this.pointOfInterest?.nativeElement), lines(this.placeToEat?.nativeElement));
        this.showAll = false;
    }
}
