import {
    AfterViewInit,
    Component,
    ElementRef,
    EventEmitter,
    Injector,
    Input,
    OnDestroy,
    OnInit,
    Output,
    ViewChild,
} from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { ICart } from '@schemas/modules/cart/cart';
import { ServiceId } from '@core/constants/service-id.const';
import { AccommodationsDataService } from '@services/modules/accommodations/dataservices/accommodations.data.service';
import { StateFrom } from '@core/constants/state-from.const';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { Room } from '@schemas/modules/accommodations/room';
import { RoomOption } from '@schemas/modules/accommodations/room-option';
import { AccommodationReviews } from '@schemas/modules/accommodations/accommodation-reviews.model';
import { BaseServiceComponent } from '../../../components/base-service-component/base-service.component';
import { UserService } from '@services/modules/user/services/user.service';
import {
    CONFIRM,
    TzDialogReturn,
} from '@shared/components/dialogs/trazler-confirmation-dialog/trazler-confirmation-dialog.component';
import { AccommodationTermsPopupComponent } from '../accommodation-terms-popup/accommodation-terms-popup.component';
import { AccommodationsService } from '@services/modules/accommodations/services/accommodations.service';
import { DateRangePipe } from '@shared/pipes/date-range.pipe';
import { TzDatePipe } from '@shared/pipes/tz-date.pipe';
import { MainDialogSize } from '@shared/schemas/dialog.model';
import { MobAccommodationAdditionalInfoComponent } from '../mob-accommodation-additional-info/mob-accommodation-additional-info.component';
import { MobAccommodationAmenitiesComponent } from '../mob-accommodation-amenities/mob-accommodation-amenities.component';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';
import { MobAccommodationReviewsComponent } from '../mob-accommodation-reviews/mob-accommodation-reviews.component';
import { MobAccommodationRulesComponent } from '../mob-accommodation-rules/mob-accommodation-rules.component';
import { IAccommodationsCartData } from '@schemas/modules/accommodations/accommodations-cart-data';
import { MobAccommodationRoomsComponent } from '../mob-accommodation-rooms/mob-accommodation-rooms.component';
import { AccommodationsUtilsService } from '@services/modules/accommodations/utils/accommodations.utils';
import { AccommodationDetailsUtilService } from '../../../accommodation-details-util.service';
// eslint-disable-next-line max-len
import { MobAccommodationRoomOfferCancelationComponent } from '../mob-accommodation-room-offer-cancelation/mob-accommodation-room-offer-cancelation.component';

@Component({
    selector: 'tz-accommodation-details',
    templateUrl: './accommodation-details.component.html',
    styleUrls: [
        './accommodation-details.component.scss',
        './accommodation-details-map.component.scss',
        './accommodation-details-booking.component.scss',
    ],
    providers: [DateRangePipe, TzDatePipe],
})
export class AccommodationDetailsComponent extends BaseServiceComponent implements OnInit, OnDestroy, AfterViewInit {
    @ViewChild('serviceBookingConfirmationComponentOfRooms') serviceBookingConfirmationComponentOfRooms?: ElementRef;
    @Input() accommodation!: Accommodation;
    @Input() cartData: IAccommodationsCartData | undefined;
    @Input() accommodationReviews: AccommodationReviews | null | undefined;
    @Input() bookingOptions: ICartSelectedItemBookingOption[] = [];
    @Input() isUserLogged!: boolean;
    @Input() fromTzMap = false;
    @Input() fromMyTrips = false;
    @Input() myTripCartData: { cartData: IAccommodationsCartData; paymentCaptureMode: number } | undefined;

    @Output() selectedRoom: EventEmitter<string> = new EventEmitter<string>();
    @Output() cancelSelection: EventEmitter<void> = new EventEmitter<void>();
    @Output() openReviewsPopup: EventEmitter<void> = new EventEmitter<void>();
    @Output() cancel: EventEmitter<string[]> = new EventEmitter<string[]>();

    public readonly maxRoomQuantity = 4;
    public cartRooms: any = [];
    public selectedRooms: { [key: number]: any } = {};
    public confirmableRoom: number | null = null;
    public descriptionMinLength = 620;
    public descriptionMaxLimit = 700;
    public showFullDescription = false;
    public roomsLeftForSelection = this.maxRoomQuantity;
    public recommendedSelected = false;
    public fromHotelsMap = false;
    public hasBooked = false;

    private cartRoomCount = 0;

    constructor(
        injector: Injector,
        private accommodationsDataService: AccommodationsDataService,
        private accommodationsService: AccommodationsService,
        private userService: UserService,
        private accommodationDetailsUtilService: AccommodationDetailsUtilService
    ) {
        super(injector);
        this.isNextSummary = this.nextIsSummary();
    }

    ngOnInit(): void {
        this.isMobile$ = this.screenSizeDetectorService.isMobile;
        this.getCartRooms();

        this.accommodationsService.updateAccInfoToList(this.accommodation);
        const mapHotel = this.accommodationsService.getMapHotel();
        this.fromHotelsMap = mapHotel !== undefined;
        this.checkRoomsSold();
        this.hasBooked = CartUtils.hasBooked(this.bookingOptions);

        this.handleExtraParams();

        const descLen = this.accommodation.description.length;
        this.showFullDescription = descLen <= this.descriptionMaxLimit;
        console.log('Hotel description length: ', descLen);
    }

    ngAfterViewInit() {
        this.scrollToConfirmationBlock();
    }

    ngOnDestroy() {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public onBackToResultsPage(backToMap?: boolean): void {
        if (!backToMap) {
            this.accommodationsService.setMapHotel(undefined);
        }

        this.cartRouterService.goToState(ServiceId.ACC, undefined, false, StateFrom.ACC_DETAILS);
    }

    public gotoSummary(): void {
        this.cartRouterService.moveToSummary();
    }

    public onToggleDescriptionText() {
        this.showFullDescription = !this.showFullDescription;
    }

    public onCancelSelection(): void {
        /// temporary solution
        this.accommodation.rooms = JSON.parse(JSON.stringify([...this.accommodation.rooms]));
        this.confirmableRoom = null;
        this.clearSelectedRooms();
        this.getCartRooms();
        this.cancelSelection.emit();
    }

    public onOpenReviewsPopup(): void {
        this.openReviewsPopup.emit();
    }

    public onRoomCountChanged($event: number, index: number) {
        this.selectedRooms[index] = $event;
        this.recommendedSelected = false;
        const roomsQuantity = AccommodationsUtilsService.getSelectedRoomQuantity(this.accommodation.rooms);
        this.roomsLeftForSelection = this.maxRoomQuantity - roomsQuantity;
        this.confirmableRoom = null;

        if ($event !== 0) {
            this.confirmableRoom = index;
        } else if (roomsQuantity > 0) {
            for (const pair in this.selectedRooms) {
                if (this.selectedRooms[pair] !== 0) {
                    this.confirmableRoom = +pair;
                }
            }
        }

        if (roomsQuantity === 0 && this.cartRoomCount > 0) {
            this.confirmableRoom = index;
        }
        this.scrollToConfirmationBlock();
    }

    public onAccommodationAddItemToCart(): void {
        if (this.cartRoomCount > 0 && this.roomsLeftForSelection === this.maxRoomQuantity) {
            this.removeService(ServiceId.ACC).then(
                (isRemoved: boolean) => {
                    console.log('remove success');
                },
                (error) => {
                    console.error(error);
                }
            );

            return;
        }

        if (this.accommodation.broker === 'Hotelbeds') {
            const rateComments: { comment: string; roomName: string }[] = [];
            this.accommodation.rooms.forEach((room: Room) => {
                if (room.roomOffers) {
                    room.roomOffers.forEach((offer: RoomOption) => {
                        if (offer.personCount > 0 && offer.rateComments !== '') {
                            const rateComment = {
                                roomName: `${room.displayName.toUpperCase()} ${
                                    offer.personCount > 1 ? 'x ' + offer.personCount : ''
                                } (${offer.meal.toUpperCase()})`,
                                comment: offer.rateComments,
                            };
                            let avail = false;
                            rateComments.forEach((rc) => {
                                if (rc.comment === rateComment.comment) {
                                    rc.roomName = rc.roomName + ', ' + rateComment.roomName;
                                    avail = true;
                                }
                            });

                            if (!avail) {
                                rateComments.push(rateComment);
                            }
                        }
                    });
                }
            });
            if (rateComments.length) {
                this.dialogService
                    .openConfirmationDialog(
                        {
                            confirmBtnTitle: this.translateService.instant('POPUPS.CONTINUE'),
                            cancelBtnTitle: this.translateService.instant('POPUPS.CANCEL'),
                            title: this.translateService.instant('POPUPS.RATE_COMMENTS.TITLE'),
                            multipleContent: [
                                /*Dummy anchor tag needed avoid the popup content scrolling to the first anchor in the rate comment*/
                                `<b>${rateComments[0].roomName}</b><a href=''></a>`,
                                rateComments[0].comment,
                            ],
                            containerClass: 'tz-hb-rate-comment',
                        },
                        { mainDialogSize: MainDialogSize.LARGE, dialogPanelClasses: ['tz-rate-comment-dialog'] }
                    )
                    .afterClosed()
                    .subscribe((dialogReturn: TzDialogReturn) => {
                        if (dialogReturn === CONFIRM) {
                            this.addItem();
                        }
                    });
            }
        } else {
            this.addItem();
        }
    }

    public addItemToCart(): void {}

    public onRoomMaximumAllowedQuantityReached(): void {
        this.dialogService.openSnackBar({
            message: this.translateService.instant('NOTIFS.SELECTION_COUNT_RESTRICTION', {
                count: this.maxRoomQuantity,
            }),
        });
    }

    public onRecommendedSelected(selected: boolean): void {
        this.recommendedSelected = selected;

        if (selected) {
            this.onCancelSelection();
        }
    }

    public onSelectRecommendedOffers() {
        const recommendedRooms: { [key: string]: number } = {};
        let roomLeftToFind = this.accommodation.numberOfRooms;

        let recOffers = this.accommodation.recommendedRooms;
        if (this.userService.IsLogged() && this.accommodation.recOffersLoggedIn) {
            recOffers = this.accommodation.recRoomsLoggedIn;
        }

        recOffers.forEach((r) => {
            recommendedRooms[r.offerId] = r.allotment;
        });

        this.accommodation.rooms.some((room) => {
            room.roomOffers.some((offer) => {
                if (recommendedRooms.hasOwnProperty(offer.id)) {
                    const allotment = recommendedRooms[offer.id];
                    offer.personCount = allotment;
                    roomLeftToFind--;
                }

                return roomLeftToFind === 0;
            });
            return roomLeftToFind === 0;
        });

        this.onAccommodationAddItemToCart();
    }

    public openTermsModal(roomOption: RoomOption) {
        const data = {
            roomCancellation: roomOption.roomCancellation,
            hasPromotions: roomOption.hasPromotions,
            discounts: roomOption.discounts,
            remarks: roomOption.remarks,
            isPayOnArrival: roomOption.payAt === 'Pay on arrival',
            checkInDateTime: this.accommodation.checkIn,
        };

        if (this.platformService.isMobile) {
            this.modalService.createMainModal(MobAccommodationRoomOfferCancelationComponent, {
                title: 'HOTELS.TERMS_CONDITIONS',
                cssClass: 'mob-terms-modal',
                data: { ...data },
            });
        } else {
            this.dialogService.openMainDialog({
                component: AccommodationTermsPopupComponent,
                showCloseBtn: true,
                title: 'HOTELS.TERMS_CONDITIONS',
                data: { ...data },
            });
        }
    }

    public showRulesMob(): void {
        this.modalService.createDetailsPage(MobAccommodationRulesComponent, {
            data: {
                accommodation: {
                    hotelRules: this.accommodation.hotelRules,
                    name: this.accommodation.name,
                    checkIn: this.accommodation.checkIn,
                    checkOut: this.accommodation.checkOut,
                },
            },
            title: 'HOTELS.RULES.TITLE',
        });
    }

    public showRoomsMob(): void {
        this.accommodationDetailsUtilService.selectRecommended = false;

        const openTermsModal$: Subject<RoomOption> = new Subject<RoomOption>();
        openTermsModal$.pipe(takeUntil(this.destroy$)).subscribe((roomOption: RoomOption) => {
            this.openTermsModal(roomOption);
        });

        this.modalService
            .createDetailsPage(MobAccommodationRoomsComponent, {
                data: {
                    accommodation: this.accommodation,
                    roomsLeftForSelection: this.roomsLeftForSelection,
                    isUserLogged: this.isUserLogged,
                    ratings: this.accommodation.ratings,
                    numberOfNights: this.accommodation.numberOfNights,
                    openTermsModal$,
                },
                title: 'HOTELS.ROOMS',
            })
            .then((modal: { data: any }) => {
                if (modal.data) {
                    setTimeout(() => {
                        this.onAccommodationAddItemToCart();
                    }, 0);
                }
            });
    }

    public showAdditionalInfo(): void {
        this.modalService.createDetailsPage(MobAccommodationAdditionalInfoComponent, {
            data: { accommodationAdditionalInfo: this.accommodation.facilities },
            title: 'HOTELS.ADDITIONAL_INFO',
        });
    }

    public showAmenities(): void {
        this.modalService.createDetailsPage(MobAccommodationAmenitiesComponent, {
            data: this.accommodation.amenityGroups,
            title: 'HOTELS.AMENITIES',
        });
    }

    private getCartRooms(): void {
        this.cartRooms = {};
        const selectedRooms: { [key: string]: number } = {};
        let cartRoomCount = 0;

        for (const option of this.bookingOptions) {
            if (!option.selectedRoom || CartUtils.isFailed(option.status)) {
                continue;
            }

            const roomId = option.selectedRoom.roomId;
            if (!this.cartRooms[roomId]) {
                this.cartRooms[roomId] = [];
            }

            this.cartRooms[roomId].push(option);

            selectedRooms[roomId] = selectedRooms[roomId] ? selectedRooms[roomId] + 1 : 1;
            cartRoomCount++;
            // this.confirmableRoom = this.accommodation.rooms.findIndex((room: Room) => roomId === room.id);
        }

        if (cartRoomCount > 0) {
            let offersPresent = 0;
            for (const [i, room] of this.accommodation.rooms.entries()) {
                if (selectedRooms[room.id]) {
                    this.selectedRooms[i] = selectedRooms[room.id];
                    offersPresent += selectedRooms[room.id];
                }
            }

            if (offersPresent > 0) {
                this.roomsLeftForSelection = this.maxRoomQuantity - offersPresent;
                this.cartRoomCount = offersPresent;
            }
        }
    }

    private addItem(): void {
        this.accommodationsDataService.addAccommodationToCart(this.accommodation).subscribe(
            (response: ICart) => {
                this.confirmableRoom = null;
                this.accommodationsService.setMapHotel(undefined);
                this.userSearchService.searchCheckDep(ServiceId.ACC, this.isNextSummary, false, false, true);
            },
            (error: any) => {
                /* if (failure.status === 406) { // Rate recheck
alertBox.prompt("Room price has been changed. Please select rooms again!");
$scope.roomDetails = {};
} else if (failure.status === 304) {
// user cancel action
return
} else if (failure.status !== 410) {
if (failure.data) {
alertBox.prompt(failure.data.message)
} else {
alertBox.prompt(failure.error)
}
}
trazlerAPI.HdlHTTPError(failure, $scope.updateAccommodationScreen);*/
            }
        );
    }

    private nextIsSummary(): boolean {
        const state = this.router.getCurrentNavigation()?.extras?.state;
        const from = state ? state['from'] : '';
        return from === StateFrom.SUMMARY || from === StateFrom.TAB;
    }

    private clearSelectedRooms(): void {
        for (const room of this.accommodation.rooms) {
            room.roomOffers = room.roomOffers.map((roomOffer: RoomOption) => ({
                ...roomOffer,
                count: 0,
            }));
        }
        this.roomsLeftForSelection = this.maxRoomQuantity;
    }

    private scrollToConfirmationBlock() {
        requestAnimationFrame(() => {
            this.scrollToElement(this.serviceBookingConfirmationComponentOfRooms, { block: 'end' });
        });
    }

    private checkRoomsSold(): void {
        if (!this.accommodation.noRoom || this.fromTzMap) {
            return;
        }

        if (this.fromHotelsMap) {
            this.dialogService
                .openConfirmationDialog({
                    title: this.translateService.instant('POPUPS.HOTEL_SOLD_OUT.TITLE'),
                    multipleContent: [this.translateService.instant('POPUPS.HOTEL_SOLD_OUT.MESSAGE')],
                    confirmBtnTitle: this.translateService.instant('POPUPS.HOTEL_SOLD_OUT.CONTINUE'),
                    cancelBtnTitle: this.translateService.instant('POPUPS.HOTEL_SOLD_OUT.BACK'),
                    icon: { name: 'no-hotel-dialog', iconStyle: { 'width.px': 105.89, 'height.px': 96.4 } },
                })
                .afterClosed()
                .subscribe((confirm) => {
                    if (confirm) {
                        return;
                    }

                    this.onBackToResultsPage();
                    return;
                });
        } else {
            this.dialogService.openSnackBar({
                message: this.translateService.instant('NOTIFS.ACC_NOT_AVAILABLE'),
            });
        }
    }

    private handleExtraParams() {
        if (this.platformService.isMobileDevice) {
            const state = history.state;
            console.log('state');
            console.log(state);

            if (state && state.extra) {
                for (const extraAction in state.extra) {
                    if (state.extra.hasOwnProperty(extraAction)) {
                        switch (extraAction) {
                            case 'changeTickets':
                            case 'changeRoom':
                                this.showRoomsMob();
                                break;
                        }
                    }
                }
            }
        }
    }
}
