import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { RoomOptionDiscounts, RoomOptionRemarks } from '@schemas/modules/accommodations/room-option';
import { Cancellation } from '@schemas/modules/core/cancelation';
import { TzCurrencyPipe } from '@shared/pipes/tz-currency.pipe';
import { TzDatePipe } from '@shared/pipes/tz-date.pipe';

@Component({
    selector: 'tz-mob-accommodation-room-offer-cancelation',
    templateUrl: './mob-accommodation-room-offer-cancelation.component.html',
    styleUrls: ['./mob-accommodation-room-offer-cancelation.component.scss'],
})
export class MobAccommodationRoomOfferCancelationComponent implements OnInit {
    @Input() data!: {
        roomCancellation: Cancellation;
        hasPromotions: boolean;
        discounts: RoomOptionDiscounts[];
        remarks: RoomOptionRemarks[];
        isPayOnArrival: boolean;
        checkInDateTime: string;
    };
    public otherPolicies: { msg: string }[] = [];
    public policyMsg = '';

    constructor(
        private translateService: TranslateService,
        private datePipe: TzDatePipe,
        private tzCurrencyPipe: TzCurrencyPipe
    ) {}

    ngOnInit() {
        this.otherPolicies = this.getOtherPolicies();
        this.policyMsg = this.getPoliceMsg();
    }

    getPoliceMsg(): string {
        const policy = this.data.roomCancellation;
        let policyMsg = '';
        if (!policy) {
            return policyMsg;
        }
        const isRefundable = policy.cancellationPolicy === 'Refundable';
        const info: { date?: string; time?: string } = this.formatDate(policy.endDateTime);

        if (!isRefundable || policy.isFree) {
            policyMsg = this.translateService.instant(`${policy.messageKey}_Msg`, info);
        }

        return policyMsg;
    }

    getOtherPolicies(): { msg: string }[] {
        const policies = this.data.roomCancellation.otherPolicies;
        const checkInDateTime = this.data.checkInDateTime;

        const otherPoliciesArr: { msg: string }[] = [];
        if (!Array.isArray(policies)) {
            return otherPoliciesArr;
        }

        policies.forEach((policy, i) => {
            let date = policy.startDateTime;
            let msg = 'CANCELLATION.PolicyMsg';
            if (i === 0 && !this.data.roomCancellation.isFree) {
                date = policy.endDateTime && policy.endDateTime !== '' ? policy.endDateTime : checkInDateTime;
                msg = 'CANCELLATION.RefundablePolicyMsg';
            }

            const price = this.tzCurrencyPipe.transform(policy.price.listPrice, policy.price.listPriceCur);
            const info = {
                price,
                ...this.formatDate(date),
            };
            otherPoliciesArr.push({
                msg: this.translateService.instant(msg, info),
            });
        });

        return otherPoliciesArr;
    }

    formatDate(dateTime: string): { date: string; time: string } {
        const dateTimeRes: { date: string; time: string } = { date: '', time: '' };

        if (dateTime) {
            const date = this.datePipe.transform(dateTime, 'medium');
            dateTimeRes.date = date
                ? `${this.datePipe.transform(dateTime, 'mediumDate')} ${this.datePipe.transform(dateTime, 'shortTime')}`
                : '';
        }

        return dateTimeRes;
    }
}
