import { Component } from '@angular/core';
import { PlatformService } from '@services/platform.service';

const TOPIMGCOUNT = 4;
const MOBTOPIMGCOUNT = 5;

@Component({
    selector: 'tz-accommodation-image-skeleton-loader',
    templateUrl: './accommodation-image-skeleton-loader.component.html',
    styleUrls: ['./accommodation-image-skeleton-loader.component.scss'],
})
export class AccommodationImageSkeletonLoaderComponent {
    public hotelTopImages!: any[];

    constructor(private platformService: PlatformService) {
        this.hotelTopImages = Array(this.platformService.isMobileDevice ? MOBTOPIMGCOUNT : TOPIMGCOUNT);
    }
}
