import { Component, Injector, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { Accommodation } from '@schemas/modules/accommodations/accommodation';
import { UserService } from '@services/modules/user/services/user.service';
import { IAccommodationsCartData } from '@schemas/modules/accommodations/accommodations-cart-data';
import { skip, switchMap, takeUntil } from 'rxjs/operators';
import { AccommodationReviews } from '@schemas/modules/accommodations/accommodation-reviews.model';
import { AccommodationReviewsPopupComponent } from './components/accommodation-reviews-popup/accommodation-reviews-popup.component';
import { AccommodationsDataService } from '@services/modules/accommodations/dataservices/accommodations.data.service';
import { AccommodationsService } from '@services/modules/accommodations/services/accommodations.service';
import { BaseServiceComponent } from '../components/base-service-component/base-service.component';
import { ServiceId } from '@core/constants/service-id.const';
import { StateFrom } from '@core/constants/state-from.const';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { Room } from '@schemas/modules/accommodations/room';

@Component({
    selector: 'tz-accommodation',
    templateUrl: './accommodation.component.html',
    styleUrls: ['./accommodation.component.scss'],
})
export class AccommodationComponent extends BaseServiceComponent implements OnInit, OnDestroy {
    public accommodation: Accommodation | null = null;
    public accommodationReviews: AccommodationReviews | null | undefined;
    public cartData: IAccommodationsCartData | undefined;
    public id: string;
    public isUserLogged!: boolean;
    public isHotelInCart = false;
    public bookingOptions: any = [];

    private curChange!: Subscription;

    constructor(
        private accommodationsDataService: AccommodationsDataService,
        private accommodationsService: AccommodationsService,
        private activeRoute: ActivatedRoute,
        private userService: UserService,
        private injector: Injector
    ) {
        super(injector);
        this.isMobile$ = this.screenSizeDetectorService.isMobile;
        this.id = this.activeRoute.snapshot.params['id'];
    }

    ngOnInit(): void {
        this.loadAccommodationData();
        this.settingsService.selectedLanguageSubject.pipe(skip(1), takeUntil(this.destroy$)).subscribe(() => {
            this.loadAccommodationData();
        });
        this.curChange = this.currencyService.selectedCurrencySubject
            .pipe(skip(1), takeUntil(this.destroy$))
            .subscribe(() => {
                this.loadAccommodationData();
            });
        this.isUserLogged = this.userService.IsLogged();
        this.getCartData();
    }

    ngOnDestroy(): void {
        this.curChange?.unsubscribe();
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public onCancelSelection(): void {
        this.loadAccommodationData();
    }

    public openReviewsPopup(): void {
        this.dialogService.openAccommodationReviewsDialog(
            AccommodationReviewsPopupComponent,
            this.accommodationReviews
        );
    }

    private assignRoomDetails(result: Accommodation): Accommodation {
        if (!result) {
            return result;
        }
        if (!result.rooms) {
            result.rooms = [];
        }
        const loggedInCustomer = this.userService.getCustomerInformation();
        const loggedIn = loggedInCustomer && loggedInCustomer.id;
        const cartData = this.cartService.getSelected(ServiceId.ACC);
        const hotelSelected =
            cartData.selectedHotel &&
            cartData.selectedHotel.id === result.giataID &&
            cartData.bookingOptions.length > 0;
        result.rooms.forEach((room: Room) => {
            room.roomOffers = room.roomOffers.filter((offer) => {
                const valid = offer.level === 0 || (loggedIn && offer.level === 1) || (!loggedIn && offer.level === 2);
                if (!valid && hotelSelected) {
                    // check cart has selected room offer
                    return cartData.bookingOptions.some(
                        (option: ICartSelectedItemBookingOption) => offer.id === option.selectedRoom?.roomOfferId
                    );
                }
                return valid;
            });
        });

        if (hotelSelected) {
            let roomsLeft = 0;
            for (const room of result.rooms) {
                for (const offer of room.roomOffers) {
                    roomsLeft += offer.roomsLeft;
                }
            }
            const bookedRooms = cartData.bookingOptions.reduce(
                (accumulator: number, option: ICartSelectedItemBookingOption) => {
                    if (CartUtils.isBooked(option.status)) {
                        accumulator++;
                    }

                    return accumulator;
                },
                0
            );

            if (bookedRooms > 0 && roomsLeft <= bookedRooms) {
                this.dialogService.openConfirmationDialog({
                    title: this.translateService.instant('POPUPS.CONTACT_SUPPORT.TITLE'),
                    multipleContent: [this.translateService.instant('POPUPS.CONTACT_SUPPORT.MESSAGE')],
                    confirmBtnTitle: this.translateService.instant('POPUPS.OK'),
                });
            }
        }

        return result;
    }

    private loadAccommodationData(): void {
        this.loading = true;
        const accommodation$ = this.accommodationsService.getAccommodation(this.id);

        const accommodationReviews$ = accommodation$.pipe(
            switchMap((accommodation: Accommodation) => {
                this.accommodation = this.assignRoomDetails(accommodation);
                return this.accommodationsDataService.getAccommodationReviews(accommodation);
            })
        );

        accommodationReviews$.pipe(takeUntil(this.destroy$)).subscribe(
            (accommodationReviews: AccommodationReviews) => {
                this.accommodationReviews = accommodationReviews;
                this.loading = false;
                setTimeout(() => {
                    this.scrollToServiceContainer();
                }, 1);
            },
            (error) => {
                const cartData = this.cartService.getCartData(ServiceId.ACC);
                if (cartData.items.length && CartUtils.hasBooked(cartData.items[0].selectedItem.bookingOptions)) {
                    //Avoid infinite loop with acc details redirecting to acc list
                    // and acc list returning to acc details when acc details are in error for whatever reason
                    this.cartRouterService.goToState(ServiceId.SUMMARY, undefined, false, StateFrom.ACC_DETAILS);
                } else {
                    this.cartRouterService.goToState(ServiceId.ACC, undefined, false, StateFrom.ACC_DETAILS);
                }
            }
        );
    }

    private getCartData(): void {
        this.cartData = this.accommodationsDataService.getSelectedAccommodationsData();
        if (this.cartData?.items?.length) {
            this.bookingOptions = this.cartData.items[0].selectedItem.bookingOptions;
            this.isHotelInCart = this.id === this.cartData.items[0].selectedItem.hotelCode;
        }
    }
}
