import { Component, Inject, Input } from '@angular/core';
import { AccommodationReviews } from '@schemas/modules/accommodations/accommodation-reviews.model';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
    selector: 'tz-accommodation-reviews-popup',
    templateUrl: './accommodation-reviews-popup.component.html',
    styleUrls: ['./accommodation-reviews-popup.component.scss'],
})
export class AccommodationReviewsPopupComponent {
    public accommodationReviews: AccommodationReviews | null | undefined;

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private mdDialogRef: MatDialogRef<AccommodationReviewsPopupComponent>
    ) {
        this.accommodationReviews = data.accommodationReviews;
    }

    public onClose(): void {
        this.mdDialogRef.close();
    }
}
