import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Accommodation, RecommendedTzRooms } from '@schemas/modules/accommodations/accommodation';
import { AccommodationCheapestPrice } from '@schemas/modules/accommodations/accommodation-cheapest-price.model';
import { Room } from '@schemas/modules/accommodations/room';
import { TranslateService } from '@ngx-translate/core';
import { RoomOption } from '@schemas/modules/accommodations/room-option';
import { UserService } from '@services/modules/user/services/user.service';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-accommodation-recommended',
    templateUrl: './accommodation-recommended.component.html',
    styleUrls: ['./accommodation-recommended.component.scss'],
})
export class AccommodationRecommendedComponent implements OnInit {
    @Input() accommodation!: Accommodation;
    @Input() selectRecommended = false;

    @Output() recommendedSelected = new EventEmitter<boolean>();
    @Output() openTermsModal: EventEmitter<RoomOption> = new EventEmitter<RoomOption>();
    @Output() scrollTo: EventEmitter<string> = new EventEmitter<string>();

    public recommendedRooms: RecommendedTzRooms[] = [];
    public cheapestPrice!: AccommodationCheapestPrice;
    public taxInfo = '';
    public recommendedTitle = '';

    constructor(
        private translateService: TranslateService,
        public userService: UserService,
        public platformService: PlatformService
    ) {}

    ngOnInit() {
        this.initializeRecommendedComponent();
    }

    public toggleSelection(): void {
        this.selectRecommended = !this.selectRecommended;
        this.recommendedSelected.emit(this.selectRecommended);
    }

    public roomCapacity(capacity: number) {
        return new Array(capacity);
    }

    public onOpenTermsModal(roomOption: RoomOption) {
        this.openTermsModal.emit(roomOption);
    }

    public scrollToRoom(roomId: string): void {
        this.scrollTo.emit(roomId);
    }

    private checkShowTerms(roomOption: RoomOption): boolean {
        return (
            roomOption.roomCancellation.otherPolicies.length > 0 ||
            roomOption.roomCancellation.cancellationPolicy === 'Non-refundable' ||
            (roomOption.roomCancellation.isFree && roomOption.roomCancellation.endDateTime !== '') ||
            roomOption.payAt === 'Pay on arrival' ||
            (roomOption.discounts.length > 0 &&
                (roomOption.packaging ? this.userService.IsLogged() : !roomOption.packaging))
        );
    }

    private newRoomInfo(room: Room, offer: RoomOption, allotment: number): RecommendedTzRooms {
        return {
            roomId: room.id,
            name: room.displayName,
            image: room.images ? room.images[0] : '',
            allotment,
            offer,
            capacity: offer.capacity * allotment,
            showTerms: this.checkShowTerms(offer),
        } as RecommendedTzRooms;
    }

    private initializeRecommendedComponent(): void {
        this.cheapestPrice = this.accommodation.cheapestPrice;
        this.taxInfo = this.accommodation.taxInfo;

        const numberOfGuests = this.accommodation.numberOfGuests;
        const txt = this.translateService.instant('HOTELS.RECOMMENDED_FOR');
        const personTxt = this.translateService.instant(numberOfGuests > 1 ? 'TRAVELLER.PERSONS' : 'TRAVELLER.PERSON');
        this.recommendedTitle = `${txt} ${numberOfGuests} ${personTxt}`;

        const recommendedRooms: { [key: string]: number } = {};
        let roomLeftToFind = this.accommodation.numberOfRooms;

        let recOffers = this.accommodation.recommendedRooms;
        if (this.userService.IsLogged() && this.accommodation.recOffersLoggedIn) {
            recOffers = this.accommodation.recRoomsLoggedIn;
            this.cheapestPrice = this.accommodation.recOffersLoggedIn.cheapestPrice;
            this.taxInfo = this.accommodation.recOffersLoggedIn.taxInfo;
        }

        recOffers.forEach((r) => {
            recommendedRooms[r.offerId] = r.allotment;
        });

        this.accommodation.rooms.some((room) => {
            room.roomOffers.some((offer) => {
                if (recommendedRooms.hasOwnProperty(offer.id)) {
                    const allotment = recommendedRooms[offer.id];
                    this.recommendedRooms.push(this.newRoomInfo(room, offer, allotment));

                    roomLeftToFind--;
                }

                return roomLeftToFind === 0;
            });
            return roomLeftToFind === 0;
        });
    }
}
